.detail-page-main-services {
  width: 100%;
  margin-top: 5rem;
}
.detail-page-main-services-h1 {
  text-align: center;
  font-size: 2rem;
  font-family: Georgia, "Times New Roman", Times, serif;
  padding: 5rem 1rem 2rem;
}

.detail-page-main-services-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.detail-page-main-services-wrapper-box {
  width: 100%;
  height: 25rem;
}
.detail-page-main-services-wrapper-box-picture {
  width: 100%;
  height: 80%;
}
.detail-page-main-services-wrapper-box-servicesname {
  width: 100%;
  height: 20%;
  text-align: center;
  font-size: 1.5rem;
  line-height: 4rem;
}

@media only screen and (min-width: 481px) and (max-width: 760px) {
  .detail-page-main-services {
    width: 100%;
    margin-top: 5rem;
  }
  .detail-page-main-services-h1 {
    text-align: center;
    font-size: 2rem;
    font-family: Georgia, "Times New Roman", Times, serif;
    padding: 5rem 1rem 2rem;
  }

  .detail-page-main-services-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .detail-page-main-services-wrapper-box {
    width: 43%;
    height: 18rem;
    margin: 1rem;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .detail-page-main-services {
    width: 100%;
    margin-top: 5rem;
  }
  .detail-page-main-services-h1 {
    text-align: center;
    font-size: 2rem;
    font-family: Georgia, "Times New Roman", Times, serif;
    padding: 5rem 1rem 2rem;
  }

  .detail-page-main-services-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .detail-page-main-services-wrapper-box {
    width: 45%;
    height: 20rem;
    margin: 1rem;
  }
}
@media only screen and (min-width: 1040px) {
  .detail-page-main-services {
    width: 100%;
    margin-top: 10rem;
  }
  .detail-page-main-services-h1 {
    text-align: center;
    font-size: 2rem;
    font-family: Georgia, "Times New Roman", Times, serif;
    padding: 5rem 1rem 2rem;
  }
  .detail-page-main-services-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .detail-page-main-services-wrapper-box {
    width: 25%;
    height: 25rem;
    margin: 1rem;
    border-top-left-radius: 0.2rem;
    border-top-right-radius: 0.2rem;
  }
  .detail-page-main-services-wrapper-box-picture {
    width: 100%;
    height: 80%;
    transition: transform 0.5s ease;
  }
  .detail-page-main-services-wrapper-box-picture:hover {
    transform: scale(1.1);
  }
  .detail-page-main-services-wrapper-box-servicesname {
    width: 100%;
    height: 20%;
    text-align: center;
    font-size: 1.5rem;
    line-height: 4rem;
  }
}
