.detail-page-main-picture {
  width: 100%;
  height: 28rem;
  background-image: url("./pictures/backupBanner.jpg");
  color: black;
  background-size: cover;
  margin-top: 1rem;
}
.detail-page-main-picture-h1:first-child {
  font-size: 1.5rem;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  padding-top: 2rem;
}
.detail-page-main-picture-h1 {
  font-size: 1.3rem;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  text-align: center;
  padding-top: 1rem;
}
.detail-page-main-picture-button-a {
  text-decoration: none;
  color: white;
}
.detail-page-main-picture-button {
  margin: 1rem;
  padding: 1rem;
  font-size: 1.3rem;
  display: block;
  margin: 4.3rem auto;
  box-sizing: border-box;
  border: 1px solid #8279fc;
  background: #8279fc;
  color: white;
}
.detail-page-main-picture-button:hover {
  background: white;
  color: black;
  cursor: pointer;
}

/**/

.detail-page-main-things {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.detail-page-main-things-card {
  width: 100%;
  height: 17rem;
  box-sizing: border-box;
  background: #5982d4;
  color: white;
}

.detail-page-main-things-card:nth-child(2) {
  width: 100%;
  height: 15em;
  background: #759ef0;
  box-sizing: border-box;
}

.detail-page-main-things-card-h1 {
  text-align: center;
  padding: 4rem 0 1rem;
  font-size: 2rem;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}
.detail-page-main-things-card-p {
  text-align: center;
  font-size: 1.5rem;
  font-family: "Times New Roman", Times, serif;
  padding: 0 1rem;
}
@media only screen and (min-width: 481px) and (max-width: 760px) {
  .detail-page-main-picture {
    width: 100%;
    height: 28rem;
    background-image: url("./pictures/backupBanner.jpg");
    color: black;
    background-size: cover;
    margin-top: 1rem;
  }
  .detail-page-main-picture-h1:first-child {
    font-size: 1.5rem;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    text-align: center;
    padding-top: 4rem;
  }
  .detail-page-main-picture-h1 {
    font-size: 1.3rem;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    text-align: center;
    padding-top: 1rem;
  }

  .detail-page-main-things-card {
    width: 100%;
    height: 13rem;
    box-sizing: border-box;
    background: #5982d4;
    color: white;
  }

  .detail-page-main-things-card:nth-child(2) {
    width: 100%;
    height: 13em;
    background: #759ef0;
    box-sizing: border-box;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .detail-page-main-picture {
    width: 100%;
    height: 35rem;
    background-image: url("./pictures/backupBanner.jpg");
