    font-size: 1.8rem;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    text-align: center;
    padding-top: 4rem;
  }
  .detail-page-main-picture-h1 {
    font-size: 1.5rem;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    text-align: center;
    padding-top: 1rem;
  }
  .detail-page-main-picture-button {
    margin: 1rem;
    padding: 1rem;
    font-size: 1.3rem;
