    border: 1px solid #8279fc;
    background: #8279fc;
    color: white;
  }

  .detail-page-main-things {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .detail-page-main-things-card {
    width: 35%;
    height: 13rem;
    box-sizing: border-box;
    background: #5982d4;
    color: white;
  }

  .detail-page-main-things-card:nth-child(2) {
    width: 30%;
    height: 13em;
    background: #759ef0;
    box-sizing: border-box;
