  }
  .detail-page-main-things-card {
    width: 40%;
    height: 100%;
    box-sizing: border-box;
    background: #5982d4;
    color: white;
  }

  .detail-page-main-things-card:nth-child(2) {
    width: 30%;
    height: 100%;
