.detail-page-main-certified {
  width: 100%;
  margin-top: 10rem;
}
.detail-page-main-certified-h1 {
  text-align: center;
  font-size: 2rem;
  font-family: Georgia, "Times New Roman", Times, serif;
  padding: 2rem 1rem 2rem;
}
.detail-page-main-certified-wrapper {
  width: 100%;
  height: 20rem;
  box-sizing: border-box;
  display: flex;
  overflow: auto;
  scroll-behavior: smooth;
  margin: 0 auto;
}
.detail-page-main-certified-card {
  width: 100%;
  height: 100%;
  background: green;
  box-sizing: border-box;
  border: 5px solid white;
  flex-shrink: 0;
  flex-grow: 0;
}
.detail-page-main-certified-card-picture {
  width: 100%;
  height: 100%;
}
.detail-page-main-certified-controll {
  width: 80%;
  margin: 3rem auto;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
}
.detail-page-main-certified-controll-button {
  width: 35%;
  height: 4rem;
  box-sizing: border-box;
  border: 1px solid grey;
  font-size: 1.5rem;
  transition: all 0.5s ease;
  background: linear-gradient(to right, white 50%, lightgrey 50%);
  background-size: 200% 100%;
  background-position: left bottom;
}
.detail-page-main-certified-controll-button:hover {
  cursor: pointer;
  background-position: right bottom;
}
.fa-long-arrow-alt-left {
  font-size: 1.5rem;
  margin: 0 0.5rem;
}
.fa-long-arrow-alt-right {
  font-size: 1.5rem;
  margin: 0 0.5rem;
}

@media only screen and (min-width: 481px) and (max-width: 760px) {
  .detail-page-main-certified-wrapper {
