.nav-bar {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 2;
  background: white;
  transition: transform 0.1s ease;
}
.hide {
  transform: translate3d(-100vw, 0, 0);
}
.show {
  transform: translate3d(0vw, 0, 0);
}
.nav-bar-list-a {
  text-decoration: none;
  color: black;
}

.nav-bar-list {
  width: 100vw;
  height: 100vh;
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.nav-bar-list-li {
  display: inline-block;
  margin: 1.5rem 2rem;
  font-size: 2.3rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.nav-bar-list-li:hover {
  color: rgb(26, 162, 167);
}

.nav-bar-mobile {
  width: 100%;
  position: fixed;
  background: white;
  top: 0;
}
.nav-bar-mobile-ul {
  display: flex;
  list-style: none;
  justify-content: space-between;
  font-size: 1.5rem;
  align-items: center;
}
.nav-bar-mobile-ul-li {
  padding: 1rem;
}
.nav-bar-mobile-ul-li:hover {
  cursor: pointer;
}
@media only screen and (min-width: 481px) and (max-width: 760px) {
  .nav-bar {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 2;
    background: white;
    transition: transform 0.1s ease;
  }
  .hide {
    transform: translate3d(-100vw, 0, 0);
  }
  .show {
    transform: translate3d(0vw, 0, 0);
  }
  .nav-bar-list {
    width: 50%;
    height: 100vh;
    list-style-type: none;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .nav-bar-list-a {
    text-decoration: none;
    color: black;
  }
  .nav-bar-list-li {
    display: inline-block;
    margin: 1rem 2rem;
    font-size: 2rem;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  .nav-bar-list-li:hover {
    color: rgb(26, 162, 167);
  }

  .nav-bar-mobile {
    width: 100%;
    background: white;
    position: fixed;
  }
  .nav-bar-mobile-ul {
    display: flex;
    list-style: none;
    justify-content: space-between;
    font-size: 1.8rem;
  }
  .nav-bar-mobile-ul-li {
    padding: 1rem;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .nav-bar {
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 2;
    background: white;
    display: block;
  }
  .hide {
    transform: unset;
  }
  .show {
    transform: unset;
  }
  .nav-bar-list-a {
    text-decoration: none;
    color: black;
  }

  .nav-bar-list {
    width: 100%;
    height: unset;
    list-style-type: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .nav-bar-list-li {
    display: inline-block;
    margin: 1.5rem 2rem;
    font-size: 1.3em;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  .nav-bar-list-home {
    display: none;
  }

  .nav-bar-list-li:hover {
    color: rgb(26, 162, 167);
  }

  .nav-bar-mobile {
    width: 100%;
    background: red;
    display: none;
  }
  .nav-bar-mobile-ul {
    display: flex;
    list-style: none;
    justify-content: space-between;
    font-size: 1.8rem;
  }
  .nav-bar-mobile-ul-li {
    padding: 1rem;
  }
}
@media only screen and (min-width: 1040px) {
  .nav-bar-mobile {
    display: none;
  }
  .nav-bar {
    position: fixed;
    background: white;
    width: 100%;
    top: 0;
    z-index: 2;
    display: block;
  }
  .hide {
    transform: unset;
  }
  .show {
    transform: unset;
  }
  .nav-bar-list-a {
    text-decoration: none;
    color: black;
  }
  .nav-bar-list-home {
    position: absolute;
    left: 0rem;
  }
  .nav-bar-list {
    width: 100%;
    height: unset;
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
  }

  .nav-bar-list-li {
    display: inline-block;
    margin: 1.3rem 1rem;
    font-size: 1.5rem;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  .nav-bar-list-li:hover {
    color: rgb(26, 162, 167);
  }
  .nav-bar-list-li-home {
    transition: all 1s linear infinite;
  }
  .nav-bar-list-li-home:hover > .fa-arrow-up {
    animation: arrowHome 1.3s linear infinite;
  }

  @keyframes arrowHome {
    0% {
      transform: translate(0px, 0px);
    }
    50% {
      transform: translate(0px, -45px);
    }
    100% {
      transform: translate(0px, -45px);
    }
  }

  .nav-bar-list-li-doctor:hover > .fa-user-md {
    animation: shrinkMd 1.5s linear infinite;
  }
  @keyframes shrinkMd {
    0% {
      transform: scale(0.7);
    }
    50% {
      transform: scale(1);
    }
    100% {
      transform: scale(0.7);
    }
  }
  .nav-bar-list-li-services:hover > .fa-clipboard-list {
    animation: clipboard 1.5s linear infinite;
  }
  @keyframes clipboard {
    0% {
      transform: translate(-5px, 0px);
    }
    50% {
      transform: translate(5px, 0px);
    }
    100% {
      transform: translate(-5px, 0px);
    }
  }

  .nav-bar-list-li-cert:hover > .fa-certificate {
    cursor: pointer;
    animation: rotateCert 5s linear infinite;
  }

  @keyframes rotateCert {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .nav-bar-list-li-about:hover > .fa-comment-dots {
    cursor: pointer;
    animation: speechAbout 1s linear infinite;
  }

  @keyframes speechAbout {
    0% {
      transform: translate(1px, -5px);
    }
    50% {
      transform: translate(1px, 5px);
    }
    100% {
      transform: translate(1px, -5px);
    }
  }
}
