.detail-page-main-footer {
  width: 100%;
  background: #5982d4;
  margin-top: 5rem;
  color: white;
}
.detail-page-main-footer-h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
  padding: 2rem;
}
.detail-page-main-footer-divider {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.detail-page-main-footer-divider-left {
  width: 100%;
  text-align: center;
}
.detail-page-main-footer-divider-right {
  width: 100%;
  text-align: center;
  margin-top: 1rem;
}
.detail-page-main-footer-divider-right-ol {
  list-style: none;
  font-family: monospace;
  font-size: 1.5rem;
  padding: 2rem;
}
.detail-page-main-footer-divider-right-ol-li {
  padding: 0.5rem;
}
.detail-page-main-footer-divider-h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.3rem;
}

@media only screen and (min-width: 1040px) {
  .detail-page-main-footer {
    width: 100%;
    background: #5982d4;
    margin-top: 17rem;
    color: white;
  }
  .detail-page-main-footer-h1 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-align: center;
    padding: 2rem;
  }
  .detail-page-main-footer-divider {
    display: flex;
    justify-content: center;
  }
  .detail-page-main-footer-divider-left {
    width: 40%;
    text-align: center;
  }
  .detail-page-main-footer-divider-right {
    width: 40%;
    text-align: center;
  }
  .detail-page-main-footer-divider-right-ol {
    list-style: none;
    font-family: monospace;
    font-size: 1.5rem;
    padding: 2rem;
  }
  .detail-page-main-footer-divider-right-ol-li {
    padding: 0.5rem;
  }
  .detail-page-main-footer-divider-h1 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2rem;
  }
}
