.detail-page-main-about {
  width: 100%;
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-top: 5rem;
}
.detail-page-main-about-map {
  width: 100%;
  height: 20rem;
}
.detail-page-main-about-info {
  width: 100%;
}
.detail-page-main-about-info-h1 {
  font-size: 2rem;
  padding: 4rem 1rem 1rem;
  text-align: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.detail-page-main-about-info-p {
  font-size: 1.4rem;
  text-align: center;
}
.detail-page-main-about-info-list {
  margin-top: 4rem;
  margin-left: 4rem;
  list-style-type: square;
  font-size: 1.5rem;
}
.detail-page-main-about-info-list-li {
  padding: 1rem;
}
@media only screen and (min-width: 481px) and (max-width: 760px) {
  .detail-page-main-about-map {
    width: 100%;
    height: 30rem;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .detail-page-main-about-map {
    width: 100%;
    height: 30rem;
  }
}
@media only screen and (min-width: 1040px) {
  .detail-page-main-about {
    width: 100%;
    height: 40rem;
    margin-top: 10rem;
    display: flex;
    justify-content: center;
    padding-top: 5rem;
  }
  .detail-page-main-about-map {
    width: 40%;
    height: 100%;
  }
  .detail-page-main-about-info {
    width: 40%;
    height: 100%;
  }
  .detail-page-main-about-info-h1 {
    font-size: 2rem;
    padding: 4rem 1rem 1rem;
    text-align: center;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  }
  .detail-page-main-about-info-p {
    font-size: 1.4rem;
    text-align: center;
  }
  .detail-page-main-about-info-list {
    margin-top: 4rem;
    margin-left: 4rem;
    list-style-type: square;
    font-size: 1.5rem;
  }
  .detail-page-main-about-info-list-li {
    padding: 1rem;
  }
}
