.detail-page-main-doctor {
  width: 100%;
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.detail-page-main-doctor-picture {
  width: 100%;
  height: 30rem;
  background-image: url("./pictures/banner.jpg");
  background-size: cover;
  margin-top: 4rem;
}

.detail-page-main-doctor-statement {
  width: 100%;
}
.detail-page-main-doctor-statement-h1 {
  text-align: center;
  padding: 1.5rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 2rem;
  margin-top: 4rem;
}
.detail-page-main-doctor-statement-p {
  padding: 1rem;
  font-size: 1.3rem;
  line-height: 2rem;
  text-indent: 3rem;
  text-align: justify;
}
@media only screen and (min-width: 481px) and (max-width: 760px) {
  .detail-page-main-doctor {
    width: 100%;
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .detail-page-main-doctor-picture {
    width: 70%;
    height: 30rem;
    background-image: url("./pictures/banner.jpg");
    background-size: cover;
    margin-top: 4rem;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .detail-page-main-doctor {
    width: 100%;
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .detail-page-main-doctor-picture {
    width: 70%;
    height: 30rem;
    background-image: url("./pictures/banner.jpg");
    background-size: cover;
    margin-top: 4rem;
  }
}
@media only screen and (min-width: 1040px) {
  .detail-page-main-doctor {
    width: 100%;
    margin-top: 10rem;
    display: flex;
    justify-content: center;
  }
  .detail-page-main-doctor-picture {
    width: 50%;
    height: 50rem;
    background-image: url("./pictures/banner.jpg");
    background-size: cover;
    margin-top: 4rem;
  }

  .detail-page-main-doctor-statement {
    width: 50%;
  }
  .detail-page-main-doctor-statement-h1 {
    text-align: center;
    padding: 1.5rem;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
    font-size: 2rem;
    margin-top: 4rem;
  }
  .detail-page-main-doctor-statement-p {
    padding: 1rem 3rem;
    font-size: 1.3rem;
    line-height: 2rem;
    text-indent: 3rem;
  }
}
